import { DownBillInfo } from '../../entity/tracking/DownBillInfo';
import { BpmnModelJsonHelper } from '@edp-aif/common-api';

export class DownBillInfoResult extends DownBillInfo {
    voName: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.voName = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "voName", this.voName);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.voName = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "voName");
    }
}