import { ChangeDetectorRef, Component, ComponentFactoryResolver, Inject, Injector, Input, LOCALE_ID, OnInit, Optional, ReflectiveInjector, ViewContainerRef } from "@angular/core";
import { BillData, BillDataField, BillDataTable, BillSchema, BillSchemaField, BillSchemaFieldBase, BillSchemaSubField, BillSchemaTable, BpmnModelJsonHelper, DataType } from "@edp-aif/common-api";
import { BillTrackerSchemaFieldContent, BillTrackingDataShownMode, BillTrackingSettings, fullPathPrefix, JointQueryMode, JointQueryParameters, JointQueryParameterType } from "@edp-bif/common-api";
import { LoadingService } from "@farris/ui-loading";
import { MessagerService } from "@farris/ui-messager";
import { BsModalService } from "@farris/ui-modal";
import { NotifyService } from "@farris/ui-notify";
import { AppOptions, AppType, FrameworkService } from "@gsp-sys/rtf-common";
import { BillDataInfo } from "../../../entity/tracking/BillDataInfo";
import { BillInfo } from "../../../entity/tracking/BillInfo";
import { BillTrackerConstant } from "../../config/bill-tracker-constant";
import { BillTrackerLocalePipe } from "../../pipe/bill-tracker-locale.pipe";
import { BillTrackerDataInfoService } from "../../services/bill-tracker-data-info.service";
import { BillTrackerInternalUiService } from "../../services/bill-tracker-internal-ui.service";
import { Exceptions } from "../../../entity/tracking/Exceptions";

declare module '@edp-aif/common-api/lib/billkit' {
  export interface KitField {
    fieldValue?: any;
    captionPath?: string;
    fieldPath?: string;
    displayName?: string;
    order?: number;
    show?: boolean;
  }

  export interface BillSchemaTable {
    fullPath?: string;
    show?: boolean;
    order?: number;
  }
}

@Component({
  selector: "lib-bill-tracker-main-table",
  templateUrl: './basic-info.component.html',
  styleUrls: []
})
export class BasicInfoComponent implements OnInit {
  @Input("schemaTable")
  public schemaTable: BillSchemaTable;

  @Input("dataTable")
  public dataTable: BillDataTable;

  @Input("billInfo")
  public billInfo: BillInfo;

  @Input("billDataInfo")
  public billDataInfo: BillDataInfo;

  fieldsWithValue: (BillSchemaField | BillSchemaSubField)[] = [];
  childSchemaTables: BillSchemaTable[] = [];
  childDataTableMap: {
    [key: string]: BillDataTable;
  } = {};
  hasLink: boolean;
  dataType = DataType;
  secTitle: string = "";

  internalUiService: BillTrackerInternalUiService;

  constructor(
    public framework: FrameworkService,
    public dataInfoService: BillTrackerDataInfoService,
    public notify: NotifyService,
    public messager: MessagerService,
    public loading: LoadingService,
    public modal: BsModalService,
    public localePipe: BillTrackerLocalePipe,
    public vcr: ViewContainerRef,
    public inj: Injector,
    public cfr: ComponentFactoryResolver,
    public cdr: ChangeDetectorRef,
    @Optional() @Inject(LOCALE_ID) public localeId: string,
  ) {
    this.localeId = localeId || this.inj.get(LOCALE_ID);

    const inject = ReflectiveInjector.resolveAndCreate([
      BillTrackerInternalUiService
    ], this.inj);
    this.internalUiService = inject.get(BillTrackerInternalUiService, null);
  }

  ngOnInit(): void {
    // 设置section标题
    this.secTitle = this.localePipe.transform("basicInfo");
    // 表名解析
    this.secTitle = this.dataInfoService.getTableLabel(this.billInfo, this.schemaTable, this.localeId);

    // 获取带值的字段信息
    try {
      this.fieldsWithValue = this.dataInfoService.getBasicInfoFieldsWithValue(this.billInfo, this.schemaTable, this.dataTable);
    } catch (error) {
      this.fieldsWithValue = [];
      if (error == Exceptions.SCALE_FIELD_VALUE_IS_NAN) {
        this.messager.error(this.localePipe.transform("scaleFieldCantBeCastToInteger"));
      }
      throw error;
    }

    // 按order排序
    this.fieldsWithValue = this.dataInfoService.sortFieldsByOrder(this.fieldsWithValue);

    //主表数据
    const r = this.dataTable.GetRowList()[0];
    // 子表列表
    this.childSchemaTables = this.dataInfoService.getChildTables(this.billInfo, this.schemaTable);
    this.childDataTableMap = r.GetChildTableMap();

    if (this.billInfo.jointQueryParameters != null) {
      const jointQueryParameters: JointQueryParameters = JSON.parse(this.billInfo.jointQueryParameters);
      this.hasLink = (this.billInfo.jointQueryMode == JointQueryMode.APP && jointQueryParameters.funcId != null)
        || (this.billInfo.jointQueryMode == JointQueryMode.URL && jointQueryParameters.url != null);
    } else {
      this.hasLink = false;
    }
  }

  clickLink() {
    this.internalUiService.openBillDetailWithFields(this.billInfo, this.billDataInfo, this.fieldsWithValue);
    event.stopPropagation();
    return false;
  }
}
