import { BpmnModelJsonHelper } from "@edp-aif/common-api";
import { JointQueryParameterContent } from "./JointQueryParameterContent";

export class FieldJointQueryParameterContent extends JointQueryParameterContent {
    fieldContent: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.fieldContent = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "fieldContent", this.fieldContent);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.fieldContent = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "fieldContent");
    }
}