        let getCreationRuleById$: Observable<IRtSlimBillCreationProcessEntityEx> = null;
        if (this.httpService != null) {
            getCreationRuleById$ = this.httpService.request(this.getBillCreationProcessEntityExByIdMethod, `${this.getBillCreationProcessEntityExByIdUrl}/${processId}`, {}) as any;

        } else {
            getCreationRuleById$ = this.repository.proxy.request(`${this.getBillCreationProcessEntityExByIdUrl}/${processId}`, this.getBillCreationProcessEntityExByIdMethod, null);
        }
