export interface BrowserType {
    type?: "Opera" | "Safari" | "Chrome" | "IE" | "FF" | "Edge" | "Unknown";
    IEVersion?: "IE7" | "IE8" | "IE9" | "IE10" | "IE11" | "Unknown";

}

export function judgeBrowserType(): BrowserType {
    const browser: BrowserType = {
        type: "Unknown",
        IEVersion: undefined,
    };

    let userAgent = navigator.userAgent; //取得浏览器的userAgent字符串 
    let isOpera = userAgent.indexOf("Opera") > -1; //判断是否Opera浏览器 

    let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera; //判断是否IE浏览器 
    isIE = isIE || (!!window['ActiveXObject'] || "ActiveXObject" in window); //新版IE11判断是否为IE浏览器

    let isEdge = userAgent.indexOf("Windows NT 6.1; Trident/7.0;") > -1 && !isIE; //判断是否IE的Edge浏览器 
    let isFF = userAgent.indexOf("Firefox") > -1; //判断是否Firefox浏览器 
    let isSafari = userAgent.indexOf("Safari") > -1 && userAgent.indexOf("Chrome") == -1; //判断是否Safari浏览器 
    let isChrome = userAgent.indexOf("Chrome") > -1 && userAgent.indexOf("Safari") > -1; //判断Chrome浏览器 

    if (isIE) {
        browser.type = "IE";
        if (navigator.userAgent != null && !!navigator.userAgent.match(/Trident\/7\./)) {
            browser.IEVersion = "IE11";
        } else {
            let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
            reIE.test(userAgent);
            let fIEVersion = parseFloat(RegExp["$1"]);
            if (fIEVersion == 7) {
                browser.IEVersion = "IE7";
            } else if (fIEVersion == 8) {
                browser.IEVersion = "IE8";
            } else if (fIEVersion == 9) {
                browser.IEVersion = "IE9";
            } else if (fIEVersion == 10) {
                browser.IEVersion = "IE10";
            } else if (fIEVersion == 11) {
                browser.IEVersion = "IE11";
            } else {
                //IE版本过低 
                browser.IEVersion = "Unknown";
            }
        }
    } //isIE end 

    if (isFF) {
        browser.type = "FF";
    }
    if (isOpera) {
        browser.type = "Opera";
    }
    if (isSafari) {
        browser.type = "Safari";
    }
    if (isChrome) {
        browser.type = "Chrome";
    }
    if (isEdge) {
        browser.type = "Edge";
    }
    return browser;
}