import { BpmnModelJsonHelper, JsonConvertableImpl } from "@edp-aif/common-api";

export class DateTypeFormatInfo extends JsonConvertableImpl {
    displayFormat: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.displayFormat = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "displayFormat", this.displayFormat);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.displayFormat = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "displayFormat");
    }
}