import { BpmnModelJsonHelper, JsonConvertableImpl } from "@edp-aif/common-api";

export class JointQueryParameterContent extends JsonConvertableImpl {
    parameterCode: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.parameterCode = null;
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "parameterCode", this.parameterCode);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.parameterCode = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "parameterCode");
    }
}