import { Injector, ComponentFactoryResolver, NgModuleFactoryLoader, Inject, LOCALE_ID, ComponentRef, ComponentFactory, ReflectiveInjector, NgModuleFactory, Type, Optional } from '@angular/core';
import { Observable, from } from 'rxjs';
import { switchMap } from 'rxjs/operators';

import { FrameContext } from '@farris/devkit';
import { BsModalService, ModalOptions } from '@farris/ui-modal';

import { IRtSlimBillCreationProcessEntityEx } from '@edp-bif/common-api';
import { LocalePipe } from '../pipe/locale.pipe';
import { SELECTOR_ARGS_INJECTOR } from '../config/selector-args-injector.config';
import { FormGetBillCreationProcessEntityExArgs } from '../../entity/form-rest/FormGetBillCreationProcessEntityExArgs';
import { IBifCreationProcessSelectorComponent } from '../base/IBifCreationProcessSelectorComponent';
import { IBifSelectorComponent } from '../base/IBifSelectorComponent';
import { IBifCreationProcessSelectorModule } from '../base/IBifCreationProcessSelectorModule';
import { MessagerService } from '@farris/ui-messager';
import { LoadingService } from '@farris/ui-loading';
import { RuntimeFrameworkService } from '@farris/command-services';
import { AbstractBifDataSelectorService } from './bif-data-selector.service';

export abstract class AbstractUiService {
    /**
     * 内置模态框配置
     */
    protected readonly defaultModalOptions: ModalOptions = {
        title: null,
        width: 900,
        height: 500,
        buttons: null,
        showButtons: null,
    };

    protected localePipe: LocalePipe;
    protected runtimeFrameworkService: RuntimeFrameworkService;

    constructor(
        protected frameContext: FrameContext,
        protected modalService: BsModalService,
        protected messager: MessagerService,
        protected injector: Injector,
        protected cfr: ComponentFactoryResolver,
        protected loader: NgModuleFactoryLoader,
        @Inject(LOCALE_ID) protected localeId: string,
        @Optional() runtimeFrameworkService: RuntimeFrameworkService,
    ) {
        this.runtimeFrameworkService = runtimeFrameworkService || injector.get(RuntimeFrameworkService);
        this.localePipe = new LocalePipe(localeId);
    }

    protected openBillCreationProcessSelector(args: FormGetBillCreationProcessEntityExArgs, type: Type<IBifCreationProcessSelectorComponent>, modalOptions?: ModalOptions, customSelectorModuleUrl?: string, extParams?: {}): Observable<IRtSlimBillCreationProcessEntityEx> {
        if (typeof modalOptions == "string") {
            if (modalOptions == "") {
                throw "modalOptions不可为空";
            }
            modalOptions = JSON.parse(modalOptions);
        }

        modalOptions = modalOptions == null ? this.defaultModalOptions : modalOptions;
        modalOptions.title = modalOptions.title == null ? this.localePipe.transform("creationProcessSelector") : modalOptions.title;

        let cmpR: ComponentRef<IBifCreationProcessSelectorComponent> = null;
        if (customSelectorModuleUrl == null || customSelectorModuleUrl.trim() == "") {
            let cmpF: ComponentFactory<IBifCreationProcessSelectorComponent> = this.cfr.resolveComponentFactory(type);

            let inj: Injector = ReflectiveInjector.resolveAndCreate([
                { provide: SELECTOR_ARGS_INJECTOR, useValue: args.ConvertToJsonObject() },
                LoadingService,
            ], this.injector);
            cmpR = cmpF.create(inj);
            this.configModalButtons(modalOptions, cmpR);

            cmpR.instance.bsModalRef4BifSelector = this.modalService.show(cmpR, modalOptions);
            return cmpR.instance.subject4CreationProcess.asObservable();
        } else {
            return from(this.loader.load(customSelectorModuleUrl)).pipe(
                switchMap(
                    (moduleFactory: NgModuleFactory<IBifCreationProcessSelectorModule>) => {
                        const moduleRef = moduleFactory.create(this.frameContext.root.injector);
                        const cmpR$ = moduleRef.instance.createCreationProcessSelector(this.frameContext.root.injector, extParams);
                        if (cmpR$ instanceof Observable) {
                            return cmpR$.pipe(
                                switchMap(
                                    (componentRef) => {
                                        cmpR = componentRef;
                                        this.configModalButtons(modalOptions, cmpR);
                                        cmpR.instance.bsModalRef4BifSelector = this.modalService.show(cmpR, modalOptions);
                                        return cmpR.instance.subject4CreationProcess.asObservable();
                                    }
                                )
                            );
                        } else if (cmpR$ instanceof ComponentRef) {
                            cmpR = cmpR$;
                            this.configModalButtons(modalOptions, cmpR);
                            cmpR.instance.bsModalRef4BifSelector = this.modalService.show(cmpR, modalOptions);
                            return cmpR.instance.subject4CreationProcess.asObservable();
                        }
                    }
                )
            );
        }
    }

    /**
     * 配置模态框按钮事件
     * @param modalOptions
     * @param cmpR
     */
    protected configModalButtons(modalOptions: ModalOptions, cmpR: ComponentRef<IBifSelectorComponent>) {
        modalOptions.buttons = [
            {
                text: this.localePipe.transform("confirm"),
                cls: "btn btn-primary",
                handle: cmpR.instance.clickOk4BifSelector,
            },
            {
                text: this.localePipe.transform("cancel"),
                cls: "btn btn-secondary",
                handle: cmpR.instance.clickClose4BifSelector,
            }
        ];
        modalOptions.showButtons = true;
    }
}
