import { Pipe, PipeTransform, Inject, LOCALE_ID } from '@angular/core';
import { BillSchemaField, BillSchemaSubField } from '@edp-aif/common-api';
import { BillTrackerDataInfoService } from '../services/bill-tracker-data-info.service';

@Pipe({ name: 'billTrackerFieldLabel' })
export class BillTrackerFieldLabelPipe implements PipeTransform {
    constructor(
        public dataInfoService: BillTrackerDataInfoService,
        @Inject(LOCALE_ID) public localeId: string,
    ) {
    }
    transform(f: BillSchemaField | BillSchemaSubField, langCode?: string): any {
        return this.dataInfoService.getFieldLabel(f, langCode || this.localeId);
    }
}
