/**
 * 单据追踪图流程图配置
 */
export class BillTrackerGraphOptions {
    /**
     * 上查按钮图片
     */
    upSearchImage?: string;
    /**
     * 下查按钮图片
     */
    downSearchImage?: string;

    /**
     * 上折按钮图片
     */
    upFolderImage?: string;

    /**
     * 下折按钮图片
     */
    downFolderImage?: string;

    /**
     * 节点宽度
     */
    nodeWidth?: number = 100;
    /**
     * 节点高度
     */
    nodeHeight?: number = 60;

    /**
     * 上查/下查/上折/下折按钮宽度
     */
    overlayWidth?: number = 10;
    /**
     * 上查/下查/上折/下折按钮高度
     */
    overlayHeight?: number = 10;

    /**
     * 显示直接关联的下游单据
     */
    showDownBills?: boolean = true;

    /**
     * 显示直接关联的上游单据
     */
    showUpBills?: boolean = true;

    // /**
    //  * 起始节点是否显示边框
    //  */
    // entryNodeShowBorder: boolean = true;
    // /**
    //  * 起始节点边框颜色
    //  */
    // entryNodeBorderColor: string = '#5d5d5d';
    // /**
    //  * 起始节点边框宽度
    //  */
    // entryNodeBorderWidth: string = '1';

    // /**
    //  * 非起始节点是否显示边框
    //  */
    // nonEntryNodeShowBorder: boolean = true;
    // /**
    //  * 非起始节点边框颜色
    //  */
    // nonEntryNodeBorderColor: string = '#000000';
    // /**
    //  * 非起始节点边框宽度
    //  */
    // nonEntryNodeBorderWidth: string = '1';

    [key: string]: any;
}