import { JsonConvertableImpl, BpmnModelJsonHelper } from '@edp-aif/common-api';

export class BillData extends JsonConvertableImpl {
    name: string;
    value: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.name = null;
        this.value = null;
    }

    public ConvertToJsonObject(): {} {
        let obj: {} = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "name", this.name);
        BpmnModelJsonHelper.SetAttributeValue(obj, "value", this.value);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.name = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "name");
        this.value = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "value");
    }
}