import { BpmnModelJsonHelper, JsonConvertableImpl } from "@edp-aif/common-api";
import { BillDetailInfo } from "./BillDetailInfo";

export class BillItemInfo extends JsonConvertableImpl {
    itemId: string;
    billDetailInfos: BillDetailInfo[] = [];

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.itemId = null;
        this.billDetailInfos = [];
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "itemId", this.itemId);
        BpmnModelJsonHelper.SetAttributeValue(obj, "billDetailInfos", this.billDetailInfos);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.itemId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "itemId");
        this.billDetailInfos = BpmnModelJsonHelper.GetAttributeValueAsList(obj, "billDetailInfos", BillDetailInfo);
    }
}
