import { mxRectangle, mxRectangleShape, MxGraphNS } from '../../../ref/mxgraph';

export class BillNodeShape extends mxRectangleShape {
    constructor(bounds: MxGraphNS.mxRectangle, fill: string, stroke: string, strokewidth?: number) {
        bounds = bounds ? bounds : new mxRectangle(0, 0, 0, 0);
        fill = fill ? fill : "none";
        stroke = stroke ? stroke : "#000000";
        strokewidth = strokewidth ? strokewidth : undefined;
        super(bounds, fill, stroke, strokewidth);
    }

    init(container) {
        super.init(container);
        this.fill = "none";
        this.stroke = "#000000";
    }
}