import { ComponentFactoryResolver, Inject, Injectable, Injector, LOCALE_ID, Optional, ReflectiveInjector } from '@angular/core';
import { BillTrackerUiService } from '../bill-tracker/services/bill-tracker-ui.service';
import { GetBillsInfoArgs } from '../entity/tracking/GetBillsInfoArgs';
import { ModalOptions } from '@farris/ui-modal';
import { BillTrackerGraphOptions } from '../bill-tracker/config/bill-tracker-graph-options';
import { BillTrackerGridOptions } from '../bill-tracker/config/bill-tracker-grid-options';
import { BindingData, ViewModel, FrameContext } from '@farris/devkit';
import { Observable } from 'rxjs';
import { BillTrackerComponent } from '../bill-tracker/components/bill-tracker/bill-tracker.component';
import { canJSONParse } from '../utils/ObjectUtils';
import { MessagerService } from '@farris/ui-messager';
import { BillTrackerLocalePipe } from '../bill-tracker/pipe/bill-tracker-locale.pipe';

/**
 * 单据追踪web构件服务类
 */
@Injectable()
export class BifBillTrackerWebCmpService {
  localePipe: BillTrackerLocalePipe;

  constructor(
    public trackerUiService: BillTrackerUiService,
    @Optional() public bindingData: BindingData,
    @Optional() public viewModel: ViewModel,
    @Optional() public frameContext: FrameContext,
    @Optional() public inj: Injector,
    @Optional() public cfr: ComponentFactoryResolver,
    @Inject(LOCALE_ID) public localeId: string,
    @Optional() public messagerService: MessagerService,
  ) {
    if (inj == null) {
      const refInj = ReflectiveInjector.resolveAndCreate([], this.frameContext && this.frameContext.injector);
      this.inj = refInj;
    }

    this.cfr = cfr || this.inj.get(ComponentFactoryResolver);
    this.localeId = localeId || this.inj.get(LOCALE_ID, null) || 'zh-CHS';
    this.messagerService = messagerService || this.inj.get(MessagerService, null);
    this.localePipe = this.trackerUiService && this.trackerUiService.localePipe || new BillTrackerLocalePipe(localeId);
  }

  /**
   * 打开单据追踪弹窗
   * @param args 单据Id或者单据查询参数
   * @param maximized 是否最大化弹窗
   * @param modalOptions 弹窗配置
   * @param graphOptions 单据追踪图配置
   * @param gridOptions 单据追踪数据表配置
   * @returns billTrackerComponent 单据追踪组件 @type { Observable<BillTrackerComponent> }
   */
  public openDialog(args: string | GetBillsInfoArgs, maximized?: boolean, modalOptions?: ModalOptions, graphOptions?: BillTrackerGraphOptions, gridOptions?: BillTrackerGridOptions): Observable<BillTrackerComponent> {
    if (args == null || args == "" || (args as any).trim && (args as any).trim() == "") {
      this.messagerService.error(this.localePipe.transform("trackerArgsCantBeEmpty"));
      throw "参数args不可为空";
    }
    if (typeof args == "string" && canJSONParse(args as string)) {
      args = JSON.parse(args as string);
    } else {
      args = args as string;
    }
    maximized = maximized as any === "" || maximized == null ? undefined : maximized;
    if (maximized != null && typeof maximized != "boolean") {
      if (maximized == "false") {
        maximized = false;
      } else if (maximized == "true") {
        maximized = true;
      } else {
        maximized = undefined;
      }
    }
    modalOptions = modalOptions as any === "" || modalOptions == null ? undefined : modalOptions;
    graphOptions = graphOptions as any === "" || graphOptions == null ? undefined : graphOptions;
    gridOptions = gridOptions as any === "" || gridOptions == null ? undefined : gridOptions;

    const argz = new GetBillsInfoArgs();
    if (typeof args == "string") {
      argz.billDataId = args;
    } else {
      argz.LoadFromJsonObject(args);
    }

    return this.trackerUiService.openDefaultBillTrackerDialog(argz, maximized, modalOptions, graphOptions, gridOptions);
  }

  /**
   * 打开单据追踪App
   * @param args 单据Id或者单据查询参数
   * @param graphOptions 单据追踪图配置
   * @param gridOptions 单据追踪数据表配置
   */
  public openApp(args: string | GetBillsInfoArgs, graphOptions?: BillTrackerGraphOptions, gridOptions?: BillTrackerGridOptions) {
    if (args == null || args == "" || (args as any).trim && (args as any).trim() == "") {
      this.messagerService.error(this.localePipe.transform("trackerArgsCantBeEmpty"));
      throw "参数args不可为空";
    }
    if (typeof args == "string" && canJSONParse(args as string)) {
      args = JSON.parse(args as string);
    } else {
      args = args as string;
    }
    graphOptions = graphOptions as any === "" || graphOptions == null ? undefined : graphOptions;
    gridOptions = gridOptions as any === "" || gridOptions == null ? undefined : gridOptions;

    const argz = new GetBillsInfoArgs();
    if (typeof args == "string") {
      argz.billDataId = args;
    } else {
      argz.LoadFromJsonObject(args);
    }

    this.trackerUiService.openDefaultBillTrackerApp(argz, graphOptions, gridOptions);
  }
}
