import { mxCellOverlay, MxGraphNS } from "../../ref/mxgraph";
import { CellOverlayState } from "./CellOverlayState";

export class CellOverlay extends mxCellOverlay {
    state: CellOverlayState;

    constructor(image: MxGraphNS.mxImage, tooltip?: string, align?: any, verticalAlign?: any, offset?: MxGraphNS.mxPoint, cursor?: string, state?: CellOverlayState) {
        super(image, tooltip, align, verticalAlign, offset, cursor);
        this.state = state || CellOverlayState.Search;
    }
}