import { Injectable, ComponentFactoryResolver, Injector } from '@angular/core';
import { IBifDataSelectorComponent } from '../base/IBifDataSelectorComponent';
import { Subject } from 'rxjs';
import { BsModalRef } from '@farris/ui-modal';
import { Entity, FrameContext, ViewModel } from '@farris/devkit';

@Injectable()
export class AbstractBifDataSelectorService implements IBifDataSelectorComponent {
    /**
     * 实体数据Subject
     */
    subject4EntityDatas: Subject<Entity[]> = new Subject();
    /**
     * 数据选择器弹窗引用
     */
    bsModalRef4BifSelector: BsModalRef;
    /**
     * 点击弹窗“确定”按钮操作
     */
    clickOk4BifSelector: () => void;
    /**
     * 点击弹窗“取消”按钮操作
     */
    clickClose4BifSelector: () => void;
}

@Injectable()
export class DefaultBifDataSelectorService extends AbstractBifDataSelectorService {
    /**
     * 实体数据Subject
     */
    subject4EntityDatas: Subject<Entity[]> = new Subject();
    /**
     * 数据选择器弹窗引用
     */
    bsModalRef4BifSelector: BsModalRef;
    /**
     * 点击弹窗“确定”按钮操作
     */
    clickOk4BifSelector: () => void = () => {
        //发送选中的来源单据实体列表并关闭模态框
        this.viewModel.frameContext.repository.getById(this.viewModel.bindingData.getObject().id).subscribe(
            (e) => {
                this.subject4EntityDatas.next([e]);
                this.subject4EntityDatas.complete();
                this.bsModalRef4BifSelector.close();
            }
        );
    };
    /**
     * 点击弹窗“取消”按钮操作
     */
    clickClose4BifSelector: () => void = () => {
        this.subject4EntityDatas.complete();
        this.bsModalRef4BifSelector.close();
    };

    constructor(
        public frameContext: FrameContext,
        public viewModel: ViewModel,
        public cfr: ComponentFactoryResolver,
        public injector: Injector,
    ) {
        super();
    }
}