import { Component, OnInit, Inject, LOCALE_ID, ElementRef, ViewChild, Injector, ComponentFactoryResolver, Optional, ReflectiveInjector } from '@angular/core';
import { BsModalRef } from '@farris/ui-modal';
import { Subject, Observable } from 'rxjs';
import { SELECTOR_ARGS_INJECTOR, SelectorArgs } from '../../config/selector-args-injector.config';
import { BifDataService } from '../../service/bif-data.service';
import { IRtSlimBillCreationProcessEntityEx } from '@edp-bif/common-api';
import { FormGetBillCreationProcessEntityExArgs } from '../../../entity/form-rest/FormGetBillCreationProcessEntityExArgs';
import { IBifCreationProcessSelectorComponent } from '../../base/IBifCreationProcessSelectorComponent';
import { LoadingService } from '@farris/ui-loading';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';
import { DatagridComponent } from '@farris/ui-datagrid';
import { LocalePipe } from '../../pipe/locale.pipe';
import { MessagerService } from '@farris/ui-messager';

@Component({
  selector: 'lib-bif-pull-selector',
  templateUrl: './pull-selector.component.html',
  styleUrls: ['./pull-selector.component.css']
})
export class PullSelectorComponent implements OnInit, IBifCreationProcessSelectorComponent {
  @ViewChild('tt')
  tt: TreeTableComponent;
  ttData: TreeNode[] = [];
  ttDataRoot: TreeNode;
  ttCols: any[];
  ttIdField = 'voId';
  ttSearchFields: {
    label: string;
    value: string;
  }[] = [];
  ttSearchObj: { field: string, value: string };

  @ViewChild('dg')
  dg: DatagridComponent;
  dgData: any[] = [];
  dgCols: any[];
  dgIdField = 'id';
  localePipe: LocalePipe;

  procData: IRtSlimBillCreationProcessEntityEx[] = [];

  scene: "Push" | "Pull" = "Pull";

  /**
   * 选中的生单流程
   */
  selectedEntity: IRtSlimBillCreationProcessEntityEx;

  bsModalRef4BifSelector: BsModalRef = null;
  subject4CreationProcess: Subject<IRtSlimBillCreationProcessEntityEx> = new Subject();

  private args: FormGetBillCreationProcessEntityExArgs;
  constructor(
    @Inject(SELECTOR_ARGS_INJECTOR) args: SelectorArgs,
    private dataService: BifDataService,
    @Inject(LOCALE_ID) localeId: string,
    private loading: LoadingService,
    private elementRef: ElementRef,
    @Optional() public inj: Injector,
    @Optional() public cfr: ComponentFactoryResolver,
    @Optional() public messagerService: MessagerService,
  ) {
    this.args = new FormGetBillCreationProcessEntityExArgs();
    this.args.LoadFromJsonObject(args);
    this.localePipe = new LocalePipe(localeId);

    if (inj == null) {
      const refInj = ReflectiveInjector.resolveAndCreate([
        MessagerService,
      ]);
      this.inj = refInj;
    }
    this.cfr = cfr || this.inj.get(ComponentFactoryResolver);
    this.messagerService = messagerService || this.inj.get(MessagerService, null);
  }

  ngOnInit() {
    this.ttDataRoot = {
      id: "*",
      data: {
        voId: "*",
        voName: this.localePipe.instant("all"),
      },
      parent: null,
      expanded: true,
      children: [],
    };
    this.ttData = [this.ttDataRoot];
    this.configTtColumns();
    this.configDgColumns();
    this.configTtEvents();
    this.configDgEvents();
    this.loadItems();
  }

  clickOk4BifSelector = () => {
    if (this.selectedEntity == null) {
      this.messagerService.error(this.localePipe.instant("pleaseSelectOneCreationProcess"));
      return;
    }
    this.subject4CreationProcess.next(this.selectedEntity);
    this.subject4CreationProcess.complete();
    this.bsModalRef4BifSelector.close();
  };

  clickClose4BifSelector = () => {
    this.bsModalRef4BifSelector.close();
  };

  private loadItems(): void {
    this.loading.show({ container: this.elementRef });
    let list$: Observable<IRtSlimBillCreationProcessEntityEx[]> = null;
    if (this.args.upBillVoId == null) {
      list$ = this.dataService.getSlimBillCreationProcessEntityExesByDownBillVoId4Pull(this.args.downBillVoId);
    } else {
      list$ = this.dataService.getSlimBillCreationProcessEntityExesByUpBillVoIdAndDownBillVoId4SpecificPull(this.args.upBillVoId, this.args.downBillVoId);
    }
    list$.subscribe(
      (result: IRtSlimBillCreationProcessEntityEx[]) => {
        this.loading.close();

        this.procData = result || [];
        this.dgData = [...this.procData];

        // 组织VO树
        const voList: {
          voId: string,
          voName: string,
        }[] = [];
        this.procData.forEach(it => {
          if (voList.some(vo => vo.voId == it.upBillVoId) == false) {
            voList.push({
              voId: it.upBillVoId,
              voName: it.upBillVoName,
            })
          }
        });

        this.ttDataRoot.children = voList.map(it => {
          return {
            id: it.voId,
            data: {
              voId: it.voId,
              voName: it.voName,
            },
            expanded: true,
          }
        });
        this.ttData = [this.ttDataRoot];

        // 默认选中第一条
        if (this.dgData && this.dgData.length > 0) {
          this.selectedEntity = this.dgData[0];
          setTimeout(() => {
            this.dg.clearSelections();
            this.dg.selectRow(this.dgData[0].id);
          }, 0);
        }
      },
      (err) => {
        this.loading.close();
        throw err;
      }
    );
  }

  configTtColumns() {
    this.ttCols = [
      {
        field: 'voName', title: this.localePipe.instant('upstreamBill'), width: 120,
      },
    ];
  }

  configDgColumns() {
    this.dgCols = [
      { field: "code", title: this.localePipe.transform("processCode"), width: 120, sortable: true },
      {
        field: "name", title: this.localePipe.transform("processName"), width: 250, sortable: true,
      },
    ];
  }

  configTtEvents() {
    this.tt.nodeSelected.subscribe((ctx) => {
      //ctx:{inidex;instance;node:{data}}
      if (ctx.node.id == "*") {
        this.dgData = [...this.procData];
      } else {
        this.dgData = [...this.procData.filter(it => it.upBillVoId == ctx.node.data.voId)];
      }
      if (this.dgData && this.dgData.length > 0) {
        this.selectedEntity = this.dgData[0];
        setTimeout(() => {
          this.dg.clearSelections();
          this.dg.selectRow(this.dgData[0].id);
        }, 0);
      }
    });
  }

  configDgEvents() {
    this.dg.selectChanged.subscribe((row) => {
      this.selectedEntity = row.data;
    });
  }
}
