import { Injectable } from "@angular/core";
import { BillDataField, BillDataRow, BillDataSubField, BillDataTable, BillSchemaField, BillSchemaFieldBase, BillSchemaSubField, BillSchemaTable, BpmnModelJsonHelper, DataType } from "@edp-aif/common-api";
import { BillTrackerSchema, BillTrackerSchemaFieldContent, BillTrackingDataShownMode, BillTrackingSettings, fullPathPrefix } from "@edp-bif/common-api";
import { BillInfo } from "../../entity/tracking/BillInfo";
import { Exceptions } from "../../entity/tracking/Exceptions";

@Injectable()
export class BillTrackerDataInfoService {
  /**
   * 判断BillTrackingSettings是旧版还是新版
   * @param bts
   * @returns
   */
  public isNewBTS(bts: string | BillTrackingSettings): boolean {
    if (typeof bts == "string") {
      const temp = new BillTrackingSettings();
      temp.LoadFromJson(bts);
      bts = temp;
    }

    return bts != null && bts.version === "1.0";
  }

  public extractDataRows(rows: BillDataRow[], shownFields: BillSchemaFieldBase[], wholeFields: BillSchemaFieldBase[], idField: string) {
    let frows = [];
    for (let row of rows) {
      let obj = {};
      obj[idField] = row.dataId;
      // 先解析其他类型字段
      const decimalFFs = [];
      shownFields.forEach(f => {
        if (f.dataType == DataType.Decimal) {
          decimalFFs.push(f);
        } else {
          obj[f.fieldPath] = this.getFieldValue(f, row.GetFieldMap(), wholeFields);
        }
      });
      // 再解析浮点型字段
      decimalFFs.forEach(f => {
        obj[f.fieldPath] = this.getFieldValue(f, row.GetFieldMap(), wholeFields);
      });

      frows.push(obj);
    }

    return frows;
  }

  public extractCaptionPath(field: BillSchemaField | BillSchemaSubField) {
    let s = field.caption;
    let tmp = field as BillSchemaSubField;
    while (tmp.attachedField != null) {
      s = tmp.attachedField.caption + "." + s;
      tmp = tmp.attachedField as BillSchemaSubField;
    }
    return s;
  }

  public extractFieldPath(field: BillSchemaField | BillSchemaSubField) {
    let s = field.fieldName;
    let tmp = field as BillSchemaSubField;
    while (tmp.attachedField != null) {
      s = tmp.attachedField.fieldName + "." + s;
      tmp = tmp.attachedField as BillSchemaSubField;
    }
    return s;
  }

  /**
   *
   * @param fields 子字段列表
   * @param selectedFields 用户自定义展示字段列表
   * @param showCustomFields 是否按用户自定义字段显示
   * @returns
   */
  public extractFFields(fields: (BillSchemaField | BillSchemaSubField)[], selectedFields: BillSchemaFieldBase[], showCustomFields: boolean): (BillSchemaField | BillSchemaSubField)[] {
    let ffields: (BillSchemaField | BillSchemaSubField)[] = [];
    fields.forEach(f => {
      f.show = true;
      //枚举或者枚举型单值UDT时默认取Name
      if (f.fieldType == "Enum" || f.fieldType == "EnumSingleUdt") {
        const tempFPath = this.extractFieldPath(f);

        let sf = null;
        if (showCustomFields) {
          // 如果字段不在已配置字段列表中则跳过
          sf = selectedFields.find((sfield) => sfield.fieldPath == tempFPath);
          if (sf == null) {
            f.show = false;
          }
        }

        f = this.setExtendProps(f, sf, tempFPath + ".Name") as BillSchemaSubField;

        ffields.push(f);
      } else if (f.GetSubFieldList() != null && f.GetSubFieldList().length > 0) {
        ffields.push(...this.extractFFields(f.GetSubFieldList(), selectedFields, showCustomFields));
      } else {
        const tempFPath = this.extractFieldPath(f);

        let sf = null;
        if (showCustomFields) {
          // 如果字段不在已配置字段列表中则跳过
          sf = selectedFields.find((sfield) => sfield.fieldPath == tempFPath);
          if (sf == null) {
            f.show = false;
          }
        }

        f = this.setExtendProps(f, sf, tempFPath) as BillSchemaSubField;

        ffields.push(f);
      }
    });

    return ffields;
  }

  /**
   * 判定有子数据的条件为：子表可显示，且子表有数据
   */
  public hasChildData(row: BillDataRow, billInfo: BillInfo) {
    let hasChildData = false;

    const schema = this.getBillTrackerSchema(billInfo);
    let children = row.GetChildTableList();
    for (let c of children) {
      let tableShow = false;
      let hasRows = false;
      let table = schema && schema[c.tableName];
      if (table != null && table.show != null) {
        tableShow = table.show;
      } else {
        // 兼容无show属性情形
        tableShow = true;
      }
      if (c.GetRowList() && c.GetRowList().length > 0) {
        hasRows = true;
      }
      hasChildData = tableShow && hasRows;
      if (hasChildData == true) {
        break;
      }
    }
    return hasChildData;
  }

  public getFieldValue(f: BillSchemaFieldBase, rdfm: { [key: string]: BillDataField; }, wholeFields?: BillSchemaFieldBase[]) {
    let fields = f.fieldPath ? f.fieldPath.split(".") : [f.fieldName];

    let bdf: BillDataField | BillDataSubField = fields && rdfm[fields[0]];
    for (let i = 1; bdf != null && i < fields.length; i++) {
      const temp = bdf.GetSubFieldMap();
      if (temp == null) {
        break;
      }
      bdf = temp[fields[i]];
    }

    const textValue = bdf ? bdf.GetTextValue() : "";
    if (textValue == null) {
      return null;
    }
    if (f.dataType == DataType.String) {
      if (f.formatInfo != null) {
        const stringMapFormatter = f.formatInfo.stringMapFormatter;
        if (stringMapFormatter != null) {
          const v = stringMapFormatter[textValue] || textValue;
          f.fieldValue = v;
          return v;
        } else {
          f.fieldValue = textValue;
          return textValue || "";
        }
      } else {
        f.fieldValue = textValue;
        return textValue || "";
      }
    } else if (f.dataType == DataType.Decimal) {
      let scale = f.scale;
      if (f.formatInfo != null) {
        if (f.formatInfo.scale != null) {
          scale = f.formatInfo.scale;
        }
        if (f.formatInfo.enableScaleExpression) {
          if (f.formatInfo.scaleExpression != null) {
            const scaleField = this.getFieldInFields(wholeFields, f.formatInfo.scaleExpression);
            const tmp = scaleField && parseInt(String(this.getFieldValue(scaleField, rdfm)));
            // const tmp = parseInt(this.getFieldValueInFieldsWithValue(extactedFields, f.formatInfo.scaleExpression));
            if (tmp != null) {
              if (isNaN(tmp)) {
                throw Exceptions.SCALE_FIELD_VALUE_IS_NAN;
              }
              scale = tmp;
            }
          }
        }
      }
      f.fieldValue = parseFloat(String(textValue));
      if (scale < 0) {
        scale = 0;
      } else if (scale > 100) {
        scale = 100;
      }
      let dec = parseFloat(String(textValue)).toFixed(scale).toString();
      return dec || "";
    } else if (f.dataType == DataType.Boolean) {
      if (textValue == "") {
        f.fieldValue = null;
        return textValue;
      }
      f.fieldValue = JSON.parse(textValue) as boolean;
      return JSON.parse(textValue) as boolean;
    } else if (f.dataType == DataType.DateTime) {
      let date = BpmnModelJsonHelper.DateTimeFromString(textValue);
      let formatter = "yyyy-MM-dd";
      formatter = f.formatInfo && f.formatInfo.dateFormatter || "yyyy-MM-dd";
      // if (f.fieldType == "DateTime") {
      //     formatter = f.formatInfo && f.formatInfo.dateFormatter || "yyyy-MM-dd HH:mm:ss.SSS";
      // } else if (f.fieldType == "Date") {
      //     formatter = f.formatInfo && f.formatInfo.dateFormatter || "yyyy-MM-dd";
      // } else {
      //     formatter = f.formatInfo && f.formatInfo.dateFormatter || "yyyy-MM-dd";
      // }
      f.fieldValue = date;
      return this.dateFormat(date, formatter);
    } else {
      f.fieldValue = textValue;
      return textValue || "";
    }
  }

  /**
   * 根据fullPath在字段列表中查找目标字段
   * @param fields
   * @param fullPath
   * @returns
   */
  public getFieldInFields(fields: BillSchemaFieldBase[], fullPath: string) {
    let v = null;
    for (let fv of fields) {
      const curFv = fv as (BillSchemaField | BillSchemaSubField);
      // 查找根字段
      let tmpFv: BillSchemaFieldBase = fv;
      while (tmpFv.attachedField) {
        tmpFv = tmpFv.attachedField;
      }
      const rootFv = tmpFv as BillSchemaField;
      // 更新fullPath
      curFv.fullPath = fullPathPrefix + rootFv.attachedTable.tableName + "." + curFv.fieldPath;
      // 对比当前字段fullPath与目标fullPath
      if (fullPath == curFv.fullPath) {
        v = curFv;
        break;
      }
      // 递归查找子字段
      const subFlds = curFv.GetSubFieldList();
      if (subFlds && subFlds.length > 0) {
        v = this.getFieldInFields(subFlds, fullPath);
        if (v != null) {
          break;
        }
      }
    }
    return v;
  }

  public getFieldValueInFieldsWithValue(fieldsWithValue: BillSchemaFieldBase[], fullPath: string) {
    let v = null;
    for (let fv of fieldsWithValue) {
      while (fv.attachedField) {
        fv = fv.attachedField;
      }
      const rootFv = fv as BillSchemaField;
      rootFv.fullPath = fullPathPrefix + rootFv.attachedTable.tableName + "." + rootFv.fieldPath;
      if (fullPath == rootFv.fullPath) {
        v = rootFv.fieldValue;
        break;
      }
    }
    return v;
  }

  public getBasicInfoFieldsWithValue(billInfo: BillInfo, schemaTable: BillSchemaTable, dataTable: BillDataTable, filterFields: boolean = true): (BillSchemaField | BillSchemaSubField)[] {
    const fieldsWithValue: (BillSchemaField | BillSchemaSubField)[] = [];
    // 单据追踪配置
    let bts: BillTrackingSettings = new BillTrackingSettings();
    if (billInfo && billInfo.billTrackingSettings) {
      bts.LoadFromJson(billInfo.billTrackingSettings);
    }

    const schema = this.getBillTrackerSchema(billInfo);
    let selectedFields = schema && schema[schemaTable.tableName] && schema[schemaTable.tableName].fields;
    selectedFields = selectedFields ? selectedFields : [];
    let showCustomFields = bts.billTrackingDataShownMode === BillTrackingDataShownMode.CUSTOMFIELD;

    // 所有字段
    const fs = schemaTable.getFieldList();
    const r = dataTable.GetRowList()[0];
    // 主表字段
    const rdfm = r.GetFieldMap();

    // 提取展示字段
    const ffs = this.extractFFields(fs, selectedFields, showCustomFields);
    // 获取字段值
    // 先解析其他类型字段
    const decimalFfs = [];
    ffs.forEach(ff => {
      if (ff.dataType == DataType.Decimal) {
        decimalFfs.push(ff);
      } else {
        ff.fieldValue = this.getFieldValue(ff, rdfm);
      }
    });
    // 再解析浮点数字段
    decimalFfs.forEach(ff => {
      ff.fieldValue = this.getFieldValue(ff, rdfm, fs);
    });
    fieldsWithValue.push(...ffs);

    return fieldsWithValue;
  }

  public setExtendProps(f: BillSchemaField | BillSchemaSubField, sf: BillSchemaField | BillSchemaSubField, fieldPath: string, rdfm?: { [key: string]: BillDataField; }) {
    f.captionPath = this.extractCaptionPath(f);
    f.fieldPath = fieldPath;
    if (sf) {
      f.displayName = sf.displayName;
      f.i18nCols = sf.i18nCols;
      f.formatInfo = sf.formatInfo;
      f.order = sf.order;
    }
    if (rdfm) {
      f.fieldValue = this.getFieldValue(f, rdfm);
    }

    return f;
  }

  /**
   * 字段列表按order属性排序
   * @param fieldsWithValue
   * @returns
   */
  public sortFieldsByOrder(fieldsWithValue: (BillSchemaField | BillSchemaSubField)[]): (BillSchemaField | BillSchemaSubField)[] {
    return fieldsWithValue.sort((a, b) => {
      if (a.order != null && b.order != null) {
        return a.order - b.order;
      } else if (a.order != null && b.order == null) {
        return -1;
      } else if (a.order == null && b.order != null) {
        return 1;
      } else {
        return 0;
      }
    })
  }

  public dateFormat(date: Date, fmt: string): string {
    let ret;
    const opt = {
      "y+": date.getUTCFullYear().toString(),        // 年
      "M+": (date.getUTCMonth() + 1).toString(),     // 月
      "d+": date.getUTCDate().toString(),            // 日
      "H+": date.getUTCHours().toString(),           // 时
      "m+": date.getUTCMinutes().toString(),         // 分
      "s+": date.getUTCSeconds().toString(),         // 秒
      "S+": date.getUTCMilliseconds().toString(),    // 秒
      // 有其他格式化字符需求可以继续添加，必须转化成字符串
    };
    for (let k in opt) {
      ret = new RegExp("(" + k + ")").exec(fmt);
      if (ret) {
        if (!String.prototype.padStart) {
          fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (this.myPadStart(opt[k], ret[1].length, "0")));
        } else {
          fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")));
        }
      }
    }
    return fmt;
  }

  public myPadStart(src: string, targetLength: number, padString: string) {
    targetLength = targetLength >> 0; //floor if number or convert non-number to 0;
    padString = String(typeof padString !== 'undefined' ? padString : ' ');
    if (src.length > targetLength) {
      return String(src);
    } else {
      targetLength = targetLength - src.length;
      if (targetLength > padString.length) {
        padString += padString.repeat(targetLength / padString.length); //append to original to ensure we are longer than needed
      }
      return padString.slice(0, targetLength) + String(src);
    }
  }

  /**
   * 解析字段名称
   * @param f
   * @param langCode
   * @returns
   */
  public getFieldLabel(f: BillSchemaField | BillSchemaSubField, langCode?: string) {
    return (langCode && f.i18nCols && f.i18nCols.colI18nValues.displayName &&
      f.i18nCols.colI18nValues.displayName[langCode]) || f.displayName || f.captionPath || f.caption
  }

  /**
   * 解析表名称
   */
  public getTableLabel(billInfo: BillInfo, schemaTable: BillSchemaTable, langCode?: string) {
    const schema = this.getBillTrackerSchema(billInfo);
    let table = schema && schema[schemaTable.tableName];
    if (table != null) {
      const i18nCols = table.i18nCols;
      const tmp = (langCode && i18nCols && i18nCols.colI18nValues.displayName && i18nCols.colI18nValues.displayName[langCode]) || table.displayName || table.caption || table.code;
      return tmp;
    } else {
      return schemaTable.caption;
    }
  }

  /**
   * 获取解析可展示子表
   */
  public getChildTables(billInfo: BillInfo, schemaTable: BillSchemaTable) {
    let result: BillSchemaTable[] = [];

    const schema = this.getBillTrackerSchema(billInfo);
    const children = schemaTable.GetChildTableList();
    children && children.forEach((child) => {
      let trackerTable = schema && schema[child.tableName];
      if (trackerTable != null && trackerTable.show != null) {
        if (trackerTable.show == true) {
          child.order = trackerTable.order;
          result.push(child);
        }
      } else {
        // 兼容无show属性情形
        result.push(child);
      }
    });

    result = this.sortTablesByOrder(result);

    return result;
  }

  /**
   * 表按sort字段排序
   * @param schemaTables
   * @returns
   */
  public sortTablesByOrder(schemaTables: BillSchemaTable[]): BillSchemaTable[] {
    return schemaTables.sort((a, b) => {
      if (a.order != null && b.order != null) {
        return a.order - b.order;
      } else if (a.order != null && b.order == null) {
        return -1;
      } else if (a.order == null && b.order != null) {
        return 1;
      } else {
        return 0;
      }
    })
  }
  /**
   * 解析billTrackerSchema
   * @param billInfo
   */
  public getBillTrackerSchema(billInfo: BillInfo): BillTrackerSchema {
    let bts: BillTrackingSettings = new BillTrackingSettings();
    if (billInfo && billInfo.billTrackingSettings) {
      bts.LoadFromJson(billInfo.billTrackingSettings);
    }

    let fieldContent = bts.customFieldContent;
    let fieldConfig: BillTrackerSchemaFieldContent = fieldContent && (typeof fieldContent == "object" ? fieldContent : JSON.parse(fieldContent));
    let schema = fieldConfig && fieldConfig.schema;

    return schema;
  }
}
