import { Component, OnInit, ComponentFactoryResolver, Injector, AfterViewInit, ViewContainerRef, ViewChild, ComponentRef, NgModuleFactoryLoader, NgModuleFactory, ChangeDetectorRef, Optional, LOCALE_ID, Inject, ElementRef } from '@angular/core';
import { BasicTableComponent } from '../basic-table/basic-table.component';
import { BillTrackerDataService } from '../../services/bill-tracker-data.service';
import { GetMainEntityDataArgs } from '../../args/GetMainEntityDataArgs';
import { LoadingService } from '@farris/ui-loading';
import { MessagerService } from '@farris/ui-messager';
import { EntityDataResult } from '../../args/EntityDataResult';
import { BillInfo } from '../../../entity/tracking/BillInfo';
import { BillTrackingSettings, BillTrackingDataShownMode } from '@edp-bif/common-api';
import { from, of, Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { IBillTrackerDataShownModule } from '../../base/IBillTrackerDataShownModule';
import { IBillTrackerDataShownComponent } from '../../base/IBillTrackerDataShownComponent';
import { BasicInfoComponent } from '../basic-info/basic-info.component';
import { NotifyService } from '@farris/ui-notify';
import { BsModalService } from '@farris/ui-modal';
import { BillTrackerDataInfoService } from '../../services/bill-tracker-data-info.service';
import { BillTrackerLocalePipe } from '../../pipe/bill-tracker-locale.pipe';
import { BillDataInfo } from '../../../entity/tracking/BillDataInfo';

declare var System: any;

@Component({
  selector: 'lib-bill-tracker-data',
  templateUrl: './bill-tracker-data.component.html',
  styleUrls: ['./bill-tracker-data.component.css']
})
export class BillTrackerDataComponent implements OnInit, AfterViewInit {

  @ViewChild("dataRegion", { read: ViewContainerRef })
  dataRegion: ViewContainerRef;
  dataInfoService: BillTrackerDataInfoService;
  localePipe: BillTrackerLocalePipe;
  localeId: string;
  vcr: ViewContainerRef;
  er: ElementRef<HTMLDivElement>;

  constructor(
    public notify: NotifyService,
    public messager: MessagerService,
    public loading: LoadingService,
    public modal: BsModalService,
    public dataService: BillTrackerDataService,
    public inj: Injector,
    public cfr: ComponentFactoryResolver,
    public cdf: ChangeDetectorRef,
    public loader: NgModuleFactoryLoader,
    @Inject(LOCALE_ID) localeId: string,
    @Optional() vcr: ViewContainerRef,
    @Optional() er: ElementRef,
  ) {
    this.dataInfoService = new BillTrackerDataInfoService();
    this.localeId = localeId || this.inj.get(LOCALE_ID, null) || 'zh-CHS';
    this.localePipe = new BillTrackerLocalePipe(localeId);
    this.vcr = vcr || this.inj.get(ViewContainerRef, null);
    this.er = er || this.inj.get(ElementRef, null);
  }

  ngOnInit() {

  }

  ngAfterViewInit(): void {

  }

  public clearDataRegion(): void {
    this.dataRegion.clear();
  }

  public showDataGrid(args: GetMainEntityDataArgs, billInfo?: BillInfo, billDataInfo?: BillDataInfo, res?: EntityDataResult): void {
    // 单据追踪配置
    let bts: BillTrackingSettings = new BillTrackingSettings();
    if (billInfo && billInfo.billTrackingSettings) {
      bts.LoadFromJson(billInfo.billTrackingSettings);
    }
    if (bts.billTrackingDataShownMode == BillTrackingDataShownMode.CUSTOMFORM) {
      this.showCustomForm(args, billInfo, billDataInfo);
    } else {
      this.showGeneralForm(args, billInfo, billDataInfo, res);
    }
  }

  /**
   * 打开通用表单
   * @param args
   * @param billInfo
   * @param res
   */
  private showGeneralForm(args: GetMainEntityDataArgs, billInfo?: BillInfo, billDataInfo?: BillDataInfo, res?: EntityDataResult, newBTS?: boolean) {
    if (res != null) {
      this.createDataComp(res, billInfo, billDataInfo);
    } else {
      const l = this.loading.show({ message: this.localePipe.transform("loading") });
      this.dataService.getEntityData(args).subscribe(
        (res: EntityDataResult) => {
          this.createDataComp(res, billInfo, billDataInfo);
          l.close();
        },
        (err) => {
          l.close();
          this.messager.error(err.error == null ? err.message : (err.error.Message || err.error.message || err.message));
          throw err;
        }
      );
    }
  }

  /**
   * 打开自定义表单
   * @param args
   * @param billInfo
   */
  private showCustomForm(args: GetMainEntityDataArgs, billInfo?: BillInfo, billDataInfo?: BillDataInfo) {
    let bts: BillTrackingSettings = new BillTrackingSettings();
    if (billInfo && billInfo.billTrackingSettings) {
      bts.LoadFromJson(billInfo.billTrackingSettings);
    }
    let formContent = JSON.parse(bts.customFormContent);

    let moduleF$ = null;
    if (formContent.form.endsWith(".js")) {
      // url格式为[js路径]时
      moduleF$ = from(System.import(formContent.form.toLowerCase()).then(m => m)).pipe(
        switchMap(
          (m: { [key: string]: NgModuleFactory<IBillTrackerDataShownModule> }) => {
            for (let key in m) {
              return of(m[key]);
            }
            return of(null);
          }
        )
      );
    } else {
      // url格式为[js路径#模块名]时
      moduleF$ = from(this.loader.load(formContent.form));
    }

    moduleF$.subscribe(
      (moduleFactory: NgModuleFactory<IBillTrackerDataShownModule>) => {
        const moduleRef = moduleFactory.create(this.inj);

        let cmpR$ = moduleRef.instance.createDataShownComponent(this.inj, args);
        if (cmpR$ instanceof ComponentRef) {
          cmpR$ = of(cmpR$);
        }
        cmpR$.subscribe(
          (cmpR: ComponentRef<IBillTrackerDataShownComponent>) => {
            // 重置滚动条
            const elt = this.dataRegion.element.nativeElement.parentElement as HTMLElement;
            elt.scrollTo({ left: 0 });

            this.dataRegion.clear();
            this.dataRegion.insert(cmpR.hostView);
          }
        );
      }
    );
  }

  /**
 * 创建通用表组件
 * @param res
 * @param billInfo
 */
  createDataComp(res: EntityDataResult, billInfo: BillInfo, billDataInfo: BillDataInfo) {
    const newBTS = this.dataInfoService.isNewBTS(billInfo.billTrackingSettings);
    // if (newBTS) {
    try {
      this.createNewDataComp(res, billInfo, billDataInfo);
    } catch (e) {
      // 重置滚动条
      const elt = this.dataRegion.element.nativeElement.parentElement as HTMLElement;
      elt && elt.scrollTo({ left: 0 });

      this.dataRegion.clear();
      const cmf = this.cfr.resolveComponentFactory(BasicTableComponent);
      const cmpR: ComponentRef<BasicTableComponent> = this.dataRegion.createComponent(cmf);
      cmpR.instance.billInfo = billInfo;
      cmpR.instance.isMain = true;
      cmpR.instance.showMainTable(res);

      this.dataRegion.insert(cmpR.hostView);
    }
    // }

  }

  /**
 * 创建通用表组件
 * @param res
 * @param billInfo
 */
  createNewDataComp(res: EntityDataResult, billInfo?: BillInfo, billDataInfo?: BillDataInfo) {
    // 重置滚动条
    const elt = this.dataRegion.element.nativeElement.parentElement as HTMLElement;
    elt && elt.scrollTo({ left: 0 });

    this.dataRegion.clear();
    const cmf = this.cfr.resolveComponentFactory(BasicInfoComponent);
    const cmpR: ComponentRef<BasicInfoComponent> = this.dataRegion.createComponent(cmf);
    cmpR.instance.schemaTable = res.billSchema.mainTable;
    cmpR.instance.dataTable = res.billData.mainTable;
    cmpR.instance.billInfo = billInfo;
    cmpR.instance.billDataInfo = billDataInfo;

    this.dataRegion.insert(cmpR.hostView);
  }
}
