import { Component, OnInit, ViewContainerRef, Injector, ComponentFactoryResolver, ViewChild, TemplateRef, ChangeDetectionStrategy, ChangeDetectorRef } from "@angular/core";
import { BillSchemaField, BillDataTable, BillDataRow, BillDataField, BillSchemaTable, BillSchemaSubField, BillDataSubField, DataType, BpmnModelJsonHelper, BillSchemaFieldBase } from '@edp-aif/common-api';
import { BillTrackingSettings, BillTrackingDataShownMode } from "@edp-bif/common-api";
import { BillFrontField } from './BillFrontField';
import { BillFrontDataRow } from './BillFrontDataRow';
import { EntityDataResult } from '../../args/EntityDataResult';
import { BillInfo } from '../../../entity/tracking/BillInfo';

@Component({
    selector: "app-basic-table",
    styles: ["./basic-table.component.css"],
    templateUrl: "./basic-table.component.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class BasicTableComponent implements OnInit {
    billInfo: BillInfo;
    isMain: boolean;

    billSchemaTable: BillSchemaTable;
    billDataTable: BillDataTable;

    fields: BillSchemaField[];
    rows: BillDataRow[];

    ffields: BillFrontField[];
    frows: BillFrontDataRow[];

    @ViewChild("childTrTable")
    childTrTable: TemplateRef<any>;

    @ViewChild("childTrTable", { read: ViewContainerRef })
    childTrTableVCR: ViewContainerRef;

    constructor(
        public viewContainerRef: ViewContainerRef,
        public inj: Injector,
        public cfr: ComponentFactoryResolver,
        public cdr: ChangeDetectorRef,
    ) {
    }

    ngOnInit(): void {

    }

    clickHandler(item: { rowData: any, expanded: boolean }, evt: Event) {
        item['expanded'] = !item['expanded'];
    }

    showMainTable(res: EntityDataResult) {
        this.showTable(res.billSchema.mainTable, res.billData.mainTable);
    }

    showTable(schemaTable: BillSchemaTable, dataTable: BillDataTable) {
        this.billSchemaTable = schemaTable;
        this.billDataTable = dataTable;

        this.loadData(schemaTable.getFieldList(), dataTable.GetRowList());

        this.cdr.detectChanges();
    }

    loadData(billSchemaFields: BillSchemaField[], billDataRows: BillDataRow[]) {
        this.fields = billSchemaFields;
        this.rows = billDataRows;

        let bts = new BillTrackingSettings();
        if (this.billInfo && this.billInfo.billTrackingSettings) {
            bts.LoadFromJson(this.billInfo.billTrackingSettings);
        }
        if (bts.billTrackingDataShownMode == BillTrackingDataShownMode.CUSTOMFIELD) {
            this.ffields = this.extractFFieldsWithCustomFields(billSchemaFields, bts.customFieldContent);
            this.frows = this.extractFRows(billDataRows, this.ffields);
        } else {
            this.ffields = this.extractFFields(billSchemaFields);
            this.frows = this.extractFRows(billDataRows, this.ffields);
        }

    }

    /**
     * 默认展示方式
     * @param fields 
     */
    private extractFFields(fields: BillSchemaField[]): BillFrontField[] {
        let ffields: BillFrontField[] = [];
        for (let field of fields) {
            //枚举或者枚举型单值UDT时默认取Name
            if (field.fieldType == "Enum" || field.fieldType == "EnumSingleUdt") {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = this.extractCaptionPath(field);
                ff.fieldPath = this.extractFieldPath(field) + ".Name";

                ffields.push(ff);
            } else if (field.GetSubFieldList() != null && field.GetSubFieldList().length > 0) {
                ffields.push(...this.extractFSubFields(field.GetSubFieldList()));
            } else {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = this.extractCaptionPath(field);
                ff.fieldPath = this.extractFieldPath(field);

                ffields.push(ff);
            }
        }

        return ffields;
    }

    private extractFSubFields(fields: BillSchemaSubField[], parentField?: BillSchemaField): BillFrontField[] {
        let ffields: BillFrontField[] = [];
        for (let field of fields) {
            //枚举或者枚举型单值UDT时默认取Name
            if (field.fieldType == "Enum" || field.fieldType == "EnumSingleUdt") {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = this.extractCaptionPath(field);
                ff.fieldPath = this.extractFieldPath(field) + ".Name";

                ffields.push(ff);
            } else if (field.GetSubFieldList() != null && field.GetSubFieldList().length > 0) {
                ffields.push(...this.extractFSubFields(field.GetSubFieldList()));
            } else {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = this.extractCaptionPath(field);
                ff.fieldPath = this.extractFieldPath(field);

                ffields.push(ff);
            }
        }

        return ffields;
    }

    /**
     * 自定义字段展示方式
     */
    private extractFFieldsWithCustomFields(fields: BillSchemaField[], fieldContent: any): BillFrontField[] {
        // 表code
        const tableCode = this.billSchemaTable.tableName;

        // 单据追踪配置
        let bts: BillTrackingSettings = new BillTrackingSettings();
        if (this.billInfo && this.billInfo.billTrackingSettings) {
            bts.LoadFromJson(this.billInfo.billTrackingSettings);
        }

        let fieldConfig: { schema?: { selectedAll?: any;[id: string]: { code: string, caption: string, fields: any[] } }, [k: string]: any } = typeof fieldContent == "object" ? fieldContent : JSON.parse(fieldContent);

        let ffields: BillFrontField[] = [];
        for (let field of fields) {
            // 字段ID
            const fieldPath = this.extractFieldPath(field);

            let sf = null;
            for (let k in fieldConfig.schema) {
                if (k == 'selectedAll') {
                    continue;
                }
                if (k == tableCode) {
                    const s = fieldConfig.schema[k];
                    sf = s && s.fields.find((elt) => elt.fieldPath == fieldPath);
                    break;
                }
            }
            if (sf == null) {
                continue;
            }

            //枚举或者枚举型单值UDT时默认取Name
            if (field.fieldType == "Enum" || field.fieldType == "EnumSingleUdt") {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = sf.displayName || field.caption;
                ff.fieldPath = this.extractFieldPath(field) + ".Name";

                ffields.push(ff);
            } else if (field.GetSubFieldList() != null && field.GetSubFieldList().length > 0) {
                ffields.push(...this.extractFSubFieldsWithCustomFields(field.GetSubFieldList(), fieldContent, field));
            } else {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = sf.displayName || field.caption;
                ff.fieldPath = this.extractFieldPath(field);

                ffields.push(ff);
            }
        }

        return ffields;
    }

    private extractFSubFieldsWithCustomFields(fields: BillSchemaSubField[], fieldContent: any, parentField?: BillSchemaFieldBase): BillFrontField[] {
        // 表编号
        const tableCode = this.billSchemaTable.tableName;

        // 单据追踪配置
        let bts: BillTrackingSettings = new BillTrackingSettings();
        if (this.billInfo && this.billInfo.billTrackingSettings) {
            bts.LoadFromJson(this.billInfo.billTrackingSettings);
        }

        let fieldConfig: { schema?: { selectedAll?: any;[id: string]: { code: string, name: string, fields: any[] } }, [k: string]: any } = typeof fieldContent == "object" ? fieldContent : JSON.parse(fieldContent);

        let ffields: BillFrontField[] = [];
        for (let field of fields) {
            // 字段ID
            const fieldPath = this.extractFieldPath(field);

            let sf = null;
            for (let k in fieldConfig.schema) {
                if (k == 'selectedAll') {
                    continue;
                }
                if (k == tableCode) {
                    const s = fieldConfig.schema[k];
                    sf = s && s.fields.find((elt) => elt.fieldPath == fieldPath);
                    break;
                }
            }
            if (sf == null) {
                continue;
            }

            //枚举或者枚举型单值UDT时默认取Name
            if (field.fieldType == "Enum" || field.fieldType == "EnumSingleUdt") {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = sf.displayName || field.caption;
                ff.fieldPath = this.extractFieldPath(field) + ".Name";

                ffields.push(ff);
            } else if (field.GetSubFieldList() != null && field.GetSubFieldList().length > 0) {
                ffields.push(...this.extractFSubFieldsWithCustomFields(field.GetSubFieldList(), fieldContent, field));
            } else {
                let ff = new BillFrontField();
                ff.fieldName = field.fieldName;
                ff.dataType = field.dataType;
                ff.caption = field.caption;
                ff.fieldType = field.fieldType;
                ff.length = field.length;
                ff.precision = field.precision;
                ff.scale = field.scale;
                ff.captionPath = sf.displayName || field.caption;
                ff.fieldPath = this.extractFieldPath(field);

                ffields.push(ff);
            }
        }

        return ffields;
    }

    private extractCaptionPath(field: BillSchemaField | BillSchemaSubField) {
        let s = field.caption;
        let tmp = field as BillSchemaSubField;
        while (tmp.attachedField != null) {
            s = tmp.attachedField.caption + "." + s;
            tmp = tmp.attachedField as BillSchemaSubField;
        }
        return s;
    }

    private extractFieldPath(field: BillSchemaField | BillSchemaSubField) {
        let s = field.fieldName;
        let tmp = field as BillSchemaSubField;
        while (tmp.attachedField != null) {
            s = tmp.attachedField.fieldName + "." + s;
            tmp = tmp.attachedField as BillSchemaSubField;
        }
        return s;
    }

    private extractFRows(rows: BillDataRow[], ffields: BillFrontField[]) {
        let frows: BillFrontDataRow[] = [];

        for (let row of rows) {
            let fr = new BillFrontDataRow();
            fr.expanded = row.GetChildTableList().length == 0 ? null : row.attachedTable && row.attachedTable.attachedRow == null ? true : false;
            fr.hasChildren = this.hasChildren(row);
            fr.data = {};
            for (let ff of ffields) {
                fr.data[ff.fieldPath] = this.extractFieldPathValue(row.GetFieldMap(), ff.fieldPath);
            }

            frows.push(fr);
        }

        return frows;
    }

    hasChildren(row: BillDataRow): boolean {
        let flag = row.GetChildTableList() && row.GetChildTableList().length > 0;

        if (flag) {
            let cf = false;
            for (let ct of row.GetChildTableList()) {
                if (ct.GetRowList() && ct.GetRowList().length > 0) {
                    cf = true;
                    break;
                }
            }

            flag = cf;
        }


        return flag;
    }

    extractFieldPathValue(billDataFieldMap: { [key: string]: BillDataField; }, fieldPath: string): any {
        let fields = fieldPath.split(".");

        let bdf: BillDataField | BillDataSubField = billDataFieldMap[fields[0]];
        for (let i = 1; bdf != null && i < fields.length; i++) {
            const temp = bdf.GetSubFieldMap();
            if (temp == null) {
                break;
            }
            bdf = temp[fields[i]];
        }

        return bdf ? bdf.GetTextValue() : "";
    }

    getShownText(obj: { [fieldPath: string]: any }, ffield: BillFrontField) {
        if (obj[ffield.fieldPath] == null) {
            return "";
        }
        if (ffield.dataType == DataType.String) {
            return obj[ffield.fieldPath] || "";
        } else if (ffield.dataType == DataType.Decimal) {
            let dec = parseFloat(String(obj[ffield.fieldPath])).toFixed(ffield.scale).toString();
            return dec || "";
        } else if (ffield.dataType == DataType.DateTime) {
            let date = BpmnModelJsonHelper.DateTimeFromString(obj[ffield.fieldPath]);
            if (ffield.fieldType == "DateTime") {
                return this.dateFormat(date, "yyyy-MM-dd HH:mm:ss.SSS");
            } else if (ffield.fieldType == "Date") {
                return this.dateFormat(date, "yyyy-MM-dd");
            } else {
                return this.dateFormat(date, "yyyy-MM-dd");
            }
        } else {
            return obj[ffield.fieldPath] || "";
        }
    }

    isObject(obj): boolean {
        return typeof (obj) == "object";
    }

    isArray(obj): boolean {
        return Array.isArray(obj);
    }

    private dateFormat(date: Date, fmt: string): string {
        let ret;
        const opt = {
            "y+": date.getUTCFullYear().toString(),        // 年
            "M+": (date.getUTCMonth() + 1).toString(),     // 月
            "d+": date.getUTCDate().toString(),            // 日
            "H+": date.getUTCHours().toString(),           // 时
            "m+": date.getUTCMinutes().toString(),         // 分
            "s+": date.getUTCSeconds().toString(),         // 秒
            "S+": date.getUTCMilliseconds().toString(),    // 秒
            // 有其他格式化字符需求可以继续添加，必须转化成字符串
        };
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                if (!String.prototype.padStart) {
                    fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (this.myPadStart(opt[k], ret[1].length, "0")));
                } else {
                    fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")));
                }
            }
        }
        return fmt;
    }

    private myPadStart(src: string, targetLength: number, padString: string) {
        targetLength = targetLength >> 0; //floor if number or convert non-number to 0;
        padString = String(typeof padString !== 'undefined' ? padString : ' ');
        if (src.length > targetLength) {
            return String(src);
        } else {
            targetLength = targetLength - src.length;
            if (targetLength > padString.length) {
                padString += padString.repeat(targetLength / padString.length); //append to original to ensure we are longer than needed
            }
            return padString.slice(0, targetLength) + String(src);
        }
    }
}