import { BifBillTrackerWebCmpService } from './bif-bill-tracker.webcmp.service';
import { BifManualCreationSourceWebCmpService } from './bif-manual-creation-source.webcmp.service';
import { BifManualCreationTargetWebCmpService } from './bif-manual-creation-target.webcmp.service';
import { BifManualCreationDataWebCmpService } from './bif-manual-creation-data.webcmp.service';
import { BillTrackerUiService } from '../bill-tracker/services/bill-tracker-ui.service';
import { BifDataService } from '../bif-manual-creation/service/bif-data.service';
import { BifSourceUiService } from '../bif-manual-creation/service/bif-source-ui.service';
import { BifTargetUiService } from '../bif-manual-creation/service/bif-target-ui.service';
import { AbstractBifDataSelectorService, DefaultBifDataSelectorService } from '../bif-manual-creation/service/bif-data-selector.service';
import { Provider } from '@angular/core';

/**
 * 因为BifDataService,BifSourceUiService,BifTargetUiService所依赖的@farris/devkit相关
 * 服务类如FrameContext、BindingData、Repository等均在RootComponent上提供，因此直接在模
 * 块上注册BIF_MANUAL_CREATION_PROVIDERS也无法获取到上述服务类，因此
 * BIF_MANUAL_CREATION_PROVIDERS只能在RootComponent上注册。
 */
const BIF_MANUAL_CREATION_PROVIDERS: Provider[] = [
  BifDataService,
  BifSourceUiService,
  BifTargetUiService,
  { provide: AbstractBifDataSelectorService, useClass: DefaultBifDataSelectorService },
  BifManualCreationDataWebCmpService,
  BifManualCreationSourceWebCmpService,
  BifManualCreationTargetWebCmpService,
];


/**
 * 同理，如果后续BIF_BILL_TRACKER_PROVIDERS中服务类需要使用@farris/devkit时，也只能在
 * root-component上注册
 */
const BIF_BILL_TRACKER_PROVIDERS: Provider[] = [
  BillTrackerUiService,
  BifBillTrackerWebCmpService,
];

const BIF_DEVKIT_PROVIDERS = [
  ...BIF_MANUAL_CREATION_PROVIDERS,

  ...BIF_BILL_TRACKER_PROVIDERS,
];

export { BIF_MANUAL_CREATION_PROVIDERS };
export { BIF_BILL_TRACKER_PROVIDERS };
export { BIF_DEVKIT_PROVIDERS };
