import { BpmnModelJsonHelper, JsonConvertableImpl } from "@edp-aif/common-api";
import { BillItemInfo } from "./BillItemInfo";

export class BillChildInfo extends JsonConvertableImpl {
    itemCode: string;
    billItemInfos: BillItemInfo[] = [];

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.itemCode = null;
        this.billItemInfos = [];
    }

    public ConvertToJsonObject(): {} {
        let obj = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "itemCode", this.itemCode);
        BpmnModelJsonHelper.SetAttributeValue(obj, "billItemInfos", this.billItemInfos);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.itemCode = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "itemCode");
        this.billItemInfos = BpmnModelJsonHelper.GetAttributeValueAsList(obj, "billItemInfos", BillItemInfo);
    }
}
